
require('./css/common.css');//

var Vue = require('vue');
var VueRouter = require('vue-router');


Vue.use(VueRouter);
var App = Vue.extend(require('./app.vue'));
var router = new VueRouter(
	{
	  hashbang: false,  //true example.com/#!/foo/bar  false example.com/#/foo/bar
	  //abstract:true,  //
	  //
	  //history: false,   // HTML5 history  404
	  //saveScrollPosition: false
	  linkActiveClass:'custom-active-class' // http://vuejs.github.io/vue-router/en/link.html
	}
);

require('./routers')(router);



router.start(App,'#app');
window.router = router;




