module.exports = {
	 	data:function(){
	 		return {
	 			a:'',
	 			title:''
	 		}
	 	},
	 	//route
	 	route:{
			data: function(transition) {
				setTimeout(function(){
					transition.next({a:'' + JSON.stringify(this.$route.params) + ''});
				}.bind(this),3000);
			},
			activate:function(transition){
				this.$root.$set('header',this.title);
				transition.next();
			}
	 	}
	 }


